            Î» = L + (1-C) * f * sinÎ± * (Ïƒ + C*sinÏƒ*(cos2Ïƒâ‚˜+C*cosÏƒ*(-1+2*cos2Ïƒâ‚˜*cos2Ïƒâ‚˜)));
